// vmvuethisvuextoken
// getApp().globalDatatokengetApp().globalData
import config from "./config.js" // 
const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		// baseUrlapph5vue.config.js
		baseUrl: config.baseUrl,
		
	});
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// config.header.Token = '5d33018e653d897fc259b42cf022c1b3';
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		config.header.token = vm.vuex_token;
		return config; 
	};
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// originalDatatrue
		// res.statueCoderes.data.code
		if(res.code == 0) {
			return res.data;  
		} else if(res.code == 301) {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			vm.$u.toast('' + res.msg);
			return false;
		} else if(res.code == 401) {
			vm.$u.toast('' + res.msg);
			return false;
		} else if(res.code == 500) {
			vm.$u.toast('' + res.msg);
			return false;
		} else {
			// 
			return true;
		}
	};
}

export default {
	install
}